<template>
  <div class="page_wrapper" v-show="pageMixin_showPage">


    <div class="main_body animated  fadeIn">

      <el-row class="head_area  panel_area">
        <el-col :span="24">
          <span class="title">用户列表</span>
          <span class="description">游戏用户列表</span>
        </el-col>
      </el-row>


      <el-row class="panel_area">


        <el-col :span="24">
          <TableToolsBox :show-add="false" :show-chart="false" :show-down-load="true" @download="downloadFn()"
                         @refresh="refresh" @search="searchFn">
            <template slot="search">

              <el-select class="search_param" placeholder="请选择显示类型" style="width: 150px"
                         v-model="searchParameterRes.ai">
                <el-option :key="index" :label="item.text"
                           :value="item.value"
                           v-for="(item,index) of this.searchParameterSource.ai"></el-option>
              </el-select>

              <el-select class="search_param" placeholder="请选择状态" style="width: 150px" v-if="searchParameterRes.ai != 1"
                         v-model="searchParameterRes.status">
                <el-option :key="index" :label="item.text"
                           :value="item.value"
                           v-for="(item,index) of this.searchParameterSource.status"></el-option>
              </el-select>

              <el-select class="search_param" placeholder="请选择性别" style="width: 150px" v-if="searchParameterRes.ai != 1"
                         v-model="searchParameterRes.sex">
                <el-option :key="index" :label="item.text"
                           :value="item.value"
                           v-for="(item,index) of this.searchParameterSource.sex"></el-option>
              </el-select>


              <el-input class="input-with-select search_param" clearable placeholder="请输入内容"
                        style="width: 250px"
                        v-model.trim="searchParameterRes.searchValue">
                <el-select placeholder="请选择" slot="prepend" style="width: 120px"
                           v-model="searchParameterRes.searchBy">
                  <el-option :key="index" :label="item.text"
                             :value="item.value "
                             v-for="(item,index) of this.searchParameterSource.searchBy"></el-option>
                  <!--                  <el-option label="用户id" value="userid"></el-option>-->
                  <!--                  <el-option label="用户名" value="username"></el-option>-->
                </el-select>
              </el-input>
              <span v-if="searchParameterRes.ai != 1">注册时间:</span>
              <el-date-picker
                align="right"
                class="search_param"
                end-placeholder="结束日期"
                range-separator="-"
                start-placeholder="开始日期"
                style="width: 250px"
                type="daterange"
                unlink-panels
                v-if="searchParameterRes.ai != 1"
                v-model="searchParameterRes.dateRange"
                value-format="timestamp">
              </el-date-picker>

            </template>
          </TableToolsBox>
        </el-col>
      </el-row>


      <el-row :gutter="20" class="panel_area">
        <el-col :span="24">
          <el-card class="box-card padding_none">
            <el-table
              :data="tableData"
              :row-style="{cursor: 'pointer'}"
              @row-click="tableRowClickFn"
              @sort-change="tableSortFn"
              border
              row-key="user_id"
              stripe
              style="width: 100%"
            >

              <el-table-column
                align="center"
                label="#"
                type="index"
                width="50">
              </el-table-column>


              <!--              <el-table-column-->
              <!--                label="ID"-->
              <!--                prop="id"-->
              <!--              ></el-table-column>-->
              <!--            <el-table-column-->
              <!--              label="用户ID"-->
              <!--              prop="user_id"-->
              <!--            >-->
              <!--            </el-table-column>-->

              <el-table-column

                label="用户昵称"
                min-width="150"
                prop="player_name"
              >
              </el-table-column>
              <!--            <el-table-column-->
              <!--              align="center"-->
              <!--              label="OPENID"-->
              <!--              prop="re_openid"-->

              <!--            >-->
              <!--            </el-table-column>-->

              <el-table-column

                align="center"

                label="AI所属场次"
                prop="is_ai"
                v-if="searchParameterRes.ai != 0"
                width="150"
              >
              </el-table-column>
              <el-table-column
                align="center"
                label="绑定手机"
                prop="tel"
                v-else
                width="150"
              >
              </el-table-column>
              <el-table-column
                align="center"
                label="性别"
                prop="player_sex"
                v-if="searchParameterRes.ai != 1"
                width="100"
              >
              </el-table-column>
              <el-table-column
                label="充值总额"
                prop="all_charge"
                sortable="custom"

              >
              </el-table-column>
              <el-table-column
                label="抽奖获得奖杯"
                prop="all_lottery"
                sortable="custom"

              >
              </el-table-column>
              <el-table-column
                align="center"
                column-key="is_ban"
                label="状态"
                prop="is_ban"
                width="100"

              >
              </el-table-column>
              <el-table-column
                align="center"
                label="最后登录时间"
                prop="login_time"
                sortable="custom"
                width="180">
              </el-table-column>

              <el-table-column
                align="center"
                label="注册时间"
                prop="reg_time"
                sortable="custom"
                width="180">
              </el-table-column>


              <!--            <el-table-column-->
              <!--              align="center"-->
              <!--              fixed="right"-->
              <!--              label="操作"-->
              <!--              width="100">-->

              <!--              <template slot-scope="scope">-->
              <!--                <el-button @click="openDialog('edit',scope.row)" size="small" type="text">编辑</el-button>-->

              <!--                <el-button @click="openDialog('delete',scope.row)" size="small" type="text">删除</el-button>-->
              <!--              </template>-->
              <!--            </el-table-column>-->

            </el-table>
          </el-card>


        </el-col>


      </el-row>


      <el-row class="panel_area">
        <el-pagination
          :current-page="tableMixin_currentPage"
          :page-size="tableMixin_pageSize"
          :page-sizes="tableMixin_pageSizeArr"
          :total="tableMixin_total"
          @current-change="tableMixin_handleCurrentChange"
          @size-change="tableMixin_handleSizeChange"
          layout="total, sizes, prev, pager, next, jumper">
        </el-pagination>
      </el-row>


    </div>

  </div>
</template>

<script>
    import pageMixin from "@/mixin/pageMixin";
    import tableMixin from "@/mixin/tableMixin";
    import TableToolsBox from "../childComponents/TableToolsBox";

    export default {
        name: "PlayerList",
        components: {
            TableToolsBox,
        },
        mixins: [pageMixin, tableMixin],
        props: {},
        data() {
            return {
                tableData: [],//表格数据
                // tableExpandArr: [],//表格要展开的列
                tableSort: {
                    sortBy: '',//通过该列的 字段 进行排序
                    sort: "desc",//排序方式
                },
                searchParameterSource: {
                    sex: '',
                    ai: '',
                    status: [],//状态 下拉框 数据源
                    searchBy: [],//搜索字段 下拉框 数据源
                },
                searchParameterRes: {
                    status: "",//状态
                    sex: '',
                    ai: '',
                    searchBy: '',//通过该字段搜索
                    searchValue: '',//搜索值
                    dateRange: '',//时间区间
                },
                addition: {
                    currentMoney: 0,
                    allStatusMoney: [],
                    allMoney: 0
                }//一些附带参数   金额统计用的
            }
        },
        watch: {
            aiList(newValue, oldValue) {
                console.log(newValue)
                this.getTableDataSource();
            }
        },
        computed: {
            aiList: function () {
                return this.searchParameterRes.ai;

            }
        },
        methods: {
            /**
             * 点击表格row  触发
             * @param row
             * @param column
             * @param event
             */
            tableRowClickFn(row, column, event) {
                this.$router.push({name: 'PlayerInfo', query: {user_id: row.user_id}});
            },
            /**
             * 点击表格排序 触发
             * @param ob
             */
            tableSortFn(ob) {
                this.tableSort.sortBy = ob.prop;
                ob.order === 'ascending' ? this.tableSort.sort = 'asc' : this.tableSort.sort = 'desc';
                this.getTableDataSource();
            },


            /**
             * 获取 搜索相关的参数 下拉框等的 待选值
             */
            getSearchParameters() {

                this.globalMixin_request('/player_search_parameters', "get").then(res => {
                    const {code, data, message} = res;
                    if (code === 10000) {
                        this.searchParameterSource.ai = data.ai || [];
                        this.searchParameterSource.sex = data.sex || [];
                        this.searchParameterSource.status = data.status || [];
                        this.searchParameterSource.searchBy = data.searchBy || [];
                        this.searchParameterRes.searchBy = this.searchParameterSource.searchBy[0].value; //设置下拉框默认选中第一个
                        this.searchParameterRes.ai = this.searchParameterSource.ai[0].value;//设置下拉框默认选中第一个
                    }

                })


            },
            /**
             * 获取表格数据
             */
            getTableDataSource() {

                if (this.searchParameterRes.ai == '1') {

                    this.globalMixin_request('/player_ai', "get", {
                        searchBy: this.searchParameterRes.searchBy,
                        searchValue: this.searchParameterRes.searchValue,
                        status: this.searchParameterRes.status,
                        currentPage: this.tableMixin_currentPage,
                        pageSize: this.tableMixin_pageSize,
                        sortBy: this.tableSort.sortBy,//排序字段
                        sort: this.tableSort.sort,//排序规则
                    }).then(res => {
                        const {code, data, message, addition} = res;
                        if (code === 10000) {
                            this.tableData = data.data.map((item) => {

                                switch (item.is_ai) {
                                    case 1:
                                        item.is_ai = '金币初级场';
                                        break;
                                    case 2:
                                        item.is_ai = '金币中级场';
                                        break;
                                    case 3:
                                        item.is_ai = '金币高级场';
                                        break;
                                    case 11:
                                        item.is_ai = '钻石初级场';
                                        break;
                                    case 12:
                                        item.is_ai = '钻石中级场';
                                        break;
                                    case 13:
                                        item.is_ai = '钻石高级场';
                                        break;
                                }

                                return item;

                            })
                            this.tableMixin_total = data.total;
                            this.addition = addition;
                        }

                    });


                    return;

                }


                this.globalMixin_request('/player', "get", {
                    searchBy: this.searchParameterRes.searchBy,
                    status: this.searchParameterRes.status,
                    sex: this.searchParameterRes.sex,
                    searchValue: this.searchParameterRes.searchValue,
                    startTime: this.searchParameterRes.dateRange[0],
                    endTime: this.searchParameterRes.dateRange[1],
                    sortBy: this.tableSort.sortBy,//排序字段
                    sort: this.tableSort.sort,//排序规则
                    currentPage: this.tableMixin_currentPage,
                    pageSize: this.tableMixin_pageSize
                }).then(res => {
                    const {code, data, message, addition} = res;
                    if (code === 10000) {
                        this.tableData = data.data;
                        this.tableMixin_total = data.total;
                        this.addition = addition;

                    }

                })

            },

            /**
             * 清除已选/已填写 的搜索参数
             */
            clearSearchParameterRes() {
                for (let p in this.searchParameterRes) {
                    if (this.searchParameterRes.hasOwnProperty(p)) {
                        this.searchParameterRes[p] = "";
                    }
                }

                this.searchParameterRes.searchBy = this.searchParameterSource.searchBy[0].value; //设置下拉框默认选中第一个
            },
            /**
             * 刷新
             */
            refresh() {
                this.clearSearchParameterRes();
                this.resetPage();
                this.getSearchParameters();
                this.getTableDataSource();

            },
            /**
             * 执行搜索
             */
            searchFn() {
                this.resetPage();
                this.getTableDataSource();
            },
            /**
             * 导出表格
             **/
            downloadFn() {
                this.ele_notify('稍等 表格开始导出');
                window.location.href = window.API_server.API_ADDRESS + '/export_csv?type=player&token=' + encodeURIComponent(this.$store.getters.getOperatorInfo.token);
                // const startTime = this.searchParameterRes.dateRange[0];
                // const endTime = this.searchParameterRes.dateRange[1];
                // if (startTime && endTime) {
                //     this.ele_notify('稍等 表格开始导出');
                //     window.location.href = window.API_server.API_ADDRESS + '/export_csv?type=player&startTime=' + startTime + '&endTime=' + endTime + '&token=' + encodeURIComponent(this.$store.getters.getOperatorInfo.token);
                //
                // }

            },
            /**
             * 重置页面
             */
            resetPage() {
                this.tableMixin_currentPage = 1;
                this.tableExpandArr = [];//清空 打开的row

            }
        },
        created() {
            this.getSearchParameters();
            this.getTableDataSource();
        },
        mounted() {
        },
    }
</script>

<style lang='scss' scoped>
  @import "../../assets/styles/base";

  .table-expand {
    font-size: 0;
  }

  .table-expand label {
    width: 90px;
    color: #99a9bf;
  }

  .table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }

  .page_wrapper {

    .money_count {

      color: $baseColor2;

      b {
        color: gray;
        font-size: 30px;
        font-weight: normal;
      }

      h6 {
        font-weight: normal;
        color: $baseColor3;
      }

      li {
        color: gray;
        margin-bottom: 10px;
      }

    }


  }
</style>



